import { Component, OnInit, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { EVENT_SERVICE_CALL_MODE, EVENT_SERVICE_CALL_PARAMS_FLAG, EVENT_SERVICE_CALL_TYPE, TIMEOUT_WARNING_TYPE, TASK_NOTIFY_TYPE, TASK_NODE_TYPES } from '../../../common/constant/code.value';
import { paginatorUtils } from '../../../common/constant/config';
import { handleResult, handleError, findObject, isJsonString } from '../../../common/func/common-func';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { TreeNode, ConfirmationService, MenuItem, DataTable } from 'primeng/primeng';
import { ManualExceptionInformationService } from '../../manual-exception-information/manual-exception-information.service';
import { EventServiceListModel } from '../../../process-definition/process-definition-model';

@Component({
  selector: 'app-event-service-detail',
  templateUrl: './event-service-detail.component.html',
  styleUrls: ['./event-service-detail.component.scss']
})
export class EventServiceDetailComponent implements OnInit {
  msg = [];

  @Output() outValueEmitter: EventEmitter<any> = new EventEmitter();
  // 事件服务实例
  eventService: EventServiceListModel = new EventServiceListModel();
  // 调用类型
  eventServiceCallType = EVENT_SERVICE_CALL_TYPE;
  // 调用模式
  eventServiceCallMode = EVENT_SERVICE_CALL_MODE;
  // 参数获取方式
  eventServiceCallParamflag = EVENT_SERVICE_CALL_PARAMS_FLAG;




  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private manualExceptionInformationService: ManualExceptionInformationService,
  ) {

  }


  ngOnInit() {
  }




  // 根据事件服务ID获取事件服务信息
  getEventServiceDetail(id: string): void {
    if (!id) {
      return;
    }
    const bodyParams = {
      id
    };
    this.manualExceptionInformationService.selectById(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.eventService = data.pageData[0].result[0];
        });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }


  closeDialog(): void {
    this.outValueEmitter.emit(event);
  }


}