import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { Pager, RULE_ENGINE_HTTP_URL, RULE_ENGINE_CONTEXT_PATH_RULE_EDITOR, PARAM_PACKAGE, CONSTANT_PACKAGE, VARIABLE_PACKAGE, RULE_PACKAGE, RESOURCE_PACKAGE, paginatorUtils, FULL_HORIZONTAL_DATE_FORMAT } from '../../../../common/constant/config';
import { TreeNode, ConfirmationService, MenuItem } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, expandLastNode, handleResult, conversionExtToNgTree, handleResult2, findObject, formatDate } from '../../../../common/func/common-func';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { DomSanitizer } from "@angular/platform-browser";
import { ProcessInstanceManagementService } from '../../../process-instance-management/process-instance-management.service';
import { invalid } from 'moment';
import { TargetNodeModel } from '../../../process-instance-model';
import { TASK_NODE_TYPES } from '../../../../common/constant/code.value';

@Component({
  selector: 'app-target-node',
  templateUrl: './target-node.component.html',
  styleUrls: ['./target-node.component.scss'],

})
export class TargetNodeComponent implements OnInit {

  msgs = [];
  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 节点列表
  targetNodes: TargetNodeModel[] = [];
  // 选中的节点
  selectedTargetNode: TargetNodeModel = new TargetNodeModel();
  // 节点类型
  nodeTypes = TASK_NODE_TYPES;

  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private processInstanceManagementService: ProcessInstanceManagementService,
  ) {

  }

  ngOnInit() {

  }



  // 获取节点列表
  getTargetNodeList(taskId: string): void {
    const bodyParams = {
      taskId,
      tranType: ''
    };
    this.processInstanceManagementService.queryWorkflowDefNode(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.targetNodes = data.pageData[0].result;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }



  selceteTargetNode(event): void {
    this.outValue.emit(event);
  }



}
