import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ProcessInstanceComponent } from './process-instance.component';
import { ProcessInstanceManagementComponent } from './process-instance-management/process-instance-management.component';
import { CheckTaskQueryComponent } from './check-task-query/check-task-query.component';
import { ProcessMaintenanceComponent } from '../process-definition/process-maintenance/process-maintenance.component';
import { ProcessDefinitionComponent } from '../process-definition/process-definition.component';
import { HistoryInstanceComponent } from './history-instance/history-instance.component';
import { AlreadyTasksQueryComponent } from './already-tasks-query/already-tasks-query.component';
import { ManualExceptionInformationComponent } from './manual-exception-information/manual-exception-information.component';
import { MessageSendingQueryComponent } from './message-sending-query/message-sending-query.component';

export const routes: Routes = [
  {
    path: '',
    component: ProcessInstanceComponent,
    children: [
      { path: '', redirectTo: 'process-instance-management', pathMatch: 'full' },
      { path: 'process-instance-management', component: ProcessInstanceManagementComponent },
      { path: 'history-instance', component: HistoryInstanceComponent },
      { path: 'check-task-query', component: CheckTaskQueryComponent },
      { path: 'already-tasks-query', component: AlreadyTasksQueryComponent },
      { path: 'manual-exception-information', component: ManualExceptionInformationComponent },
      { path: 'message-sending-query', component: MessageSendingQueryComponent },
    ]
  }
];

export const ProcessInstanceRouting: ModuleWithProviders = RouterModule.forChild(routes);
