import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
import { PROCESS_INSTANCE_MANAGE_API, TO_DO_TASKS_API } from '../process-instance.constant';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
// import { RTS_HTTP_URL } from '../../common/constant/config';

@Injectable()
export class CheckTaskQueryService {

  constructor(
    private httpService: HttpService
  ) {
  }

  // 查询任务列表
  getCheckTasks = (params) => this.httpService.post(RTS_HTTP_URL + TO_DO_TASKS_API.getCheckTaskList, params, null);

  // 获取审批记录列表
  queryApprovedList = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_INSTANCE_MANAGE_API.queryApprovedList, params, null);

}
