import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ApprovedModel, CheckTaskModel, ProcessDefinitionTypeModel, ProcessInstanceModel, SubFlowModel, SubApprovedModel } from '../../../process-instance-model';
import { paginatorUtils, CALENDAR_CN, FULL_HORIZONTAL_DATE_FORMAT, Pager } from '../../../../common/constant/config';
import { Message, TreeNode, ConfirmationService, MenuItem } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, handleResult, formatDate } from '../../../../common/func/common-func';
import { DictionaryTypeModel } from '../../../../process-definition/process-definition-model';
import { TASK_NODE_TYPES, WORK_HANDLE_STATES, PROCESS_END_TYPES, IS_PROCESS_END, TASK_END_TYPES, PROCESS_ISTIMEOUT, WORK_STATUS_TYPE, APPROVE_RESULT } from '../../../../common/constant/code.value';
import { ProcessInstanceManagementService } from '../../../process-instance-management/process-instance-management.service';
import { WorkflowHttpService } from '../../../../../http/workflow.http.service';
import { ApprovalParamsQueryBean } from '../../../../../approval-params-query/approval-params-query.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';


@Component({
  selector: 'app-child-approval-record',
  templateUrl: './child-approval-record.component.html',
  styleUrls: ['./child-approval-record.component.scss']
})
export class ChildApprovalRecordComponent implements OnChanges {

  @Input() inValue: any;

  @Input() isChange: boolean;

  @Input() taskId: any;

  @Output() outValue: EventEmitter<String> = new EventEmitter<String>();
  tableData: any[];
  approvalParamsQueryBean: ApprovalParamsQueryBean = new ApprovalParamsQueryBean();
  first: number = 0;
  code: any[];
  showTaskId:any;
  
  outExceptionalPosition: any[];//例外审批层级
  guaranteeType: any[];//担保方式
  authSize: any[];//批断权
  preferentialSize: any[];//优惠权
  exceptionalSize: any[];//例外审批权
  ch: any = CALENDAR_CN;
  total: any;
  tasks: any;

  msgs = [];
  // 子流程审批记录列表
  subApprovalRecords: SubApprovedModel[] = [];
  //子流程节点
  subflowInstance: SubFlowModel[] = [];
  // 分页
  pager: Pager = new Pager();
  // 流程运行状态
  processWorkTypes = WORK_STATUS_TYPE;
  // 类型
  businessNodeTypes = TASK_NODE_TYPES;
  // 是否超时
  isTimeOuts = PROCESS_ISTIMEOUT;
  // 当前任务处理状态
  processState = WORK_HANDLE_STATES;
  // 审批结果
  approveResult = APPROVE_RESULT;
  // 子流程嵌套数组
  approvalRecorsArray = [
    { show: false, processId: '', flag: false, title: '子流程信息' }
  ]

  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private processInstanceManagementService: ProcessInstanceManagementService,
    public workflowHttpService: WorkflowHttpService,
    public commfunc: Commfunc,
  ) {

    this.codeValues();
    this.outExceptionalPosition = this.code['outExceptionalPosition'].slice(1);//例外审批层级
    this.guaranteeType = this.code['guaranteeType1'];//担保方式
    this.authSize = this.code['authSize'];//批断权
    this.preferentialSize = this.code['authSize'];//优惠权
    this.exceptionalSize = this.code['authSize'];//例外审批权
  }
  
  ngOnChanges() {
    this.pager.page = '1';
    this.pager.first = '0';
    this.taskId && this.getChildApprovedList(this.taskId);
  }


  // 获取审批记录列表
  getChildApprovedList(mainTaskId: string): void {
    const bodyParams = {
      mainTaskId,
      isAllNode: false,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };


    this.processInstanceManagementService.queryChildTaskList(bodyParams)
      .subscribe(
        data => {
      // console.log(data.pageData.result[0].workList[0].taskId )
          handleResult(data, this.messageService, () => {
            this.subflowInstance = data.pageData[0].result;
            this.subApprovalRecords = this.subflowInstance ? this.subflowInstance[0].workList : [];
            this.subApprovalRecords && this.subApprovalRecords.length && this.subApprovalRecords.map((item,i) => {
              // console.log(item)
                if(i+1<this.subApprovalRecords.length){
                  this.showTaskId=this.subApprovalRecords[i+1].taskId
                  // console.log(this.subApprovalRecords[i+1].taskId)
                  
                  console.log(item.taskId)
                }
             
              
              item.customdoTaskActor = item.workState === '0' ? item.taskActorsName : item.doTaskActorName;
            });



            this.pager.totalRecords = this.subflowInstance && this.subflowInstance[0].workList && Array.isArray(this.subflowInstance[0].workList) ? `${this.subflowInstance[0].workList.length}` : '';
            this.subflowInstance && this.subflowInstance.length && this.subflowInstance.map(item => {
              item.customProcessCode = item.processCode.split(":")[0];
              item.customWorkCreater = item.workCreater ? item.workCreaterName ? `${item.workCreater} (${item.workCreaterName})` : `${item.workCreater}` : '';
            });
          }, true, { errorMsg: '查询失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }


  // 分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getChildApprovedList(this.taskId);
  }

  showChildApproveList(item,i): void {
    
      // console.log(item)
    if (item.taskType === 'task' || item.taskType === 'approve') {
      
      
      this.dialog.show(this.showTaskId);
    } else if (item.taskType === 'subproc') {
      this.outValue.emit(item.taskId);
      console.log(item)
      console.log(i)
    }
  }
  dialog = {
    display: false,
    title: '审批参数',
    workflowHttpService: this.workflowHttpService,
    total: 0,
    tableData: [],
    show(taskId: string) {
      this.display = true;
      this.queryApproveRecords(taskId);
    },
    hide() {
      this.display = false;
    },
    //请求数据
    queryApproveRecords(taskId: string) {
     
      this.workflowHttpService.queryApproveRecords({ taskId: taskId }).subscribe(data => {
       
        if (data.returnCode.code == 'success') {
          this.total = data.totalSize;
          data.list.forEach(item => {
            if (item.authSize > 0) {
              item.authSize = '1';
            }
            if (item.preferentialSize > 0) {
              item.preferentialSize = '1';
            }
            if (item.exceptionalSize > 0) {
              item.exceptionalSize = '1';
            }
          });
          this.tableData = data.list || [];
          
        }
      })
    }

  }

  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }
}
