import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, paginatorUtils, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { TreeNode, MenuItem } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { ApprovedModel, ProcessInstanceModel } from '../process-instance-model';
import { formatDate, handleError, handleResult, showMessage } from '../../common/func/common-func';
import { ProcessInstanceManagementService } from './process-instance-management.service';
import { DictionaryTypeModel } from '../../process-definition/process-definition-model';
import { TASK_NODE_TYPES, WORK_HANDLE_STATES, IS_PROCESS_END, MANAGE_TASKS_TYPES, TASK_END_TYPES, FLOW_STATUS_TYPES, PROCESS_ISTIMEOUT } from '../../common/constant/code.value';

@Component({
  selector: 'app-process-instance-management',
  templateUrl: './process-instance-management.component.html',
  styleUrls: ['./process-instance-management.component.scss']

})
export class ProcessInstanceManagementComponent implements OnInit {

  msgs = [];
  CALENDAR_CN = CALENDAR_CN;
  // 查询的开始日期
  startDate: Date;
  // 流程实例列表
  approvalInstances: ProcessInstanceModel[] = [];
  // 查询条件实例
  queryApprovalInstance: ProcessInstanceModel = new ProcessInstanceModel();
  // 分页实例
  paginatorInstance: ProcessInstanceModel = new ProcessInstanceModel();
  // 表格中选中的单行流程实例
  selectedProcessInstance: ProcessInstanceModel = new ProcessInstanceModel();
  // // 控制流程版本信息显示
  // processVerInfoDisplay = false;
  // 查询到的已审批列表
  queryApproveds: ApprovedModel[] = [];
  // 流程编码
  processCodeNode: TreeNode;
  // 数据总量
  total: string;
  // 登录名
  loginSys: string = '';
  // 查询启动时间
  queryStartTime: Date;
  // 查询截止时间
  queryEndTime: Date;
  // 流程是否结束
  processEndFlags = IS_PROCESS_END;
  // 查询中选中的流程是否结束标志
  selectedEndFlag: DictionaryTypeModel = new DictionaryTypeModel();
  // 全部的流程状态
  isAllProcessStatus = TASK_END_TYPES;
  // 流程状态
  processEndTypes = FLOW_STATUS_TYPES;
  // 查询中选中的流程状态
  selectedEndType: DictionaryTypeModel = new DictionaryTypeModel();
  // 类型
  businessNodeTypes = TASK_NODE_TYPES;
  // 是否超时
  isTimeOuts = PROCESS_ISTIMEOUT;
  // 查询条件中选中的是否超时
  querySelectedIsTimeOut: DictionaryTypeModel = new DictionaryTypeModel();
  // 审批记录列表表头
  approveHeaderText: string;
  // 当前任务处理状态
  processState = WORK_HANDLE_STATES;

  dialogParam = {
    display: false,
    showModel: '',
    isApprovalRecords: false,
    isHangTask: false,
    headerTitle: ''
  };

  // 右功能键
  // items: MenuItem[];
  // 控制管理类型
  manageType = MANAGE_TASKS_TYPES;
  // 传给任务管理类型的值
  taskType: string;
  // 传递给子组件让子组件内容置空
  isRest: number = 0;
  isChange = false;
  // 控制流程审批记录模态框显隐
  approvalDisplay = false;

  constructor(
    public commfunc: Commfunc,
    private messageService: MessageService,
    private processInstanceManagementService: ProcessInstanceManagementService,

  ) { }

  ngOnInit() {
    this.loginSys = sessionStorage.getItem('sys');
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
    // this.items = [
    //   {
    //     label: '流程挂起', command: (event) => {
    //       this.taskModalShow(1);
    //     }
    //   },
    //   {
    //     label: '流程挂起恢复', command: (event) => {
    //       this.recoveryHangTask();
    //     }
    //   },
    //   {
    //     label: '流程终止', command: (event) => {
    //       this.taskModalShow(2);
    //     }
    //   },
    //   {
    //     label: '流程超时延长', command: (event) => {
    //       this.taskModalShow(7);
    //     }
    //   },
    //   {
    //     label: '流程强制终止', command: (event) => {
    //       this.taskModalShow(8);
    //     }
    //   }
    // ];
  }

  // 按照搜索条件查询
  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }

  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getProcessInstanceList(this.queryApprovalInstance.processId, this.queryApprovalInstance.processCode, this.queryApprovalInstance.proccessName, this.queryApprovalInstance.startUserId, this.selectedEndType.type, this.querySelectedIsTimeOut.type, this.queryStartTime, this.queryEndTime, this.queryApprovalInstance.orgObj);
  }


  // 获取未审批流程实例列表
  getProcessInstanceList(processId?: string, processCode?: string, proccessName?: string, startUser?: string, endType?: string, isTimeout?: string, createTimeBegin?: any, createTimeEnd?: any, orgObj?: string): void {
    const endTypes = !endType ? ['0', '1'] : [`${endType}`];
    const startTimeBegin = createTimeBegin == null ? null : formatDate(createTimeBegin, FULL_HORIZONTAL_DATE_FORMAT);
    const startTimeEnd = createTimeEnd == null ? null : formatDate(createTimeEnd, FULL_HORIZONTAL_DATE_FORMAT);
    const bodyParams = {
      processId,
      processCode,
      proccessName,
      startUser,
      // userId: this.loginSys,
      endTypes,
      endType,
      isTimeout,
      workState: '0',
      startTimeBegin,
      startTimeEnd,
      orgObj,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.processInstanceManagementService.searchProcessInstanceList(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.approvalInstances = data.pageData[0].result;

            this.total = data.pageData[0].totalRows;

            this.approvalInstances && this.approvalInstances.length && this.approvalInstances.map(item => {

              item.customSystemCode = item.processCode.split(":")[0];

              item.customStartUserId = item.startUserId ? item.startUserIdName ? `${item.startUserId} (${item.startUserIdName})` : `${item.startUserId}` : '';

              item.customOrgObj = item.orgId ? item.orgName ? `${item.orgId} (${item.orgName})` : `${item.orgId}` : '';
            });


          });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }


  getApprovedList(item): void {
    this.selectedProcessInstance = item;
    // 调用子组件onChange事件
    this.isChange = !this.isChange;
    this.approvalDisplay = true;
  }

  // 查询重置
  reset(dt): void {
    this.queryApprovalInstance.processId = null;
    this.queryApprovalInstance.processCode = null;
    this.queryApprovalInstance.proccessName = null;
    this.queryApprovalInstance.startUserId = null;
    this.queryApprovalInstance.orgObj = null;
    this.selectedEndType = new DictionaryTypeModel();
    this.querySelectedIsTimeOut = new DictionaryTypeModel();
    this.queryStartTime = null;
    this.queryEndTime = null;
    this.queryFirst(dt);
  }

  // 查看流程进度
  viewProcessSchedule(item): void {
    const processCodeParam = encodeURIComponent(item.processCode);
    const params = `/${item.processCode}/${item.processId}/${item.workState}/userId001/sysCode001/tokenCode001`;
    this.processInstanceManagementService.toActivitiSchedule(params);
  }

  // 任务模态框显示
  taskModalShow(index: number): void {
    if (this.selectedProcessInstance && (this.selectedProcessInstance.endType === '1')) {
      showMessage(this.messageService, 'error', '此流程已经挂起,要进行其他操作前请先挂起恢复！');
      return;
    }
    this.isRest++;
    this.dialogParam.isApprovalRecords = false;
    this.dialogParam.isHangTask = true;
    this.dialogParam.headerTitle = this.manageType[index].descp;
    this.taskType = this.manageType[index].type;
    this.dialogParam.display = true;
  }
  // 任务管理保存后关闭模态框
  callBack(event): void {
    this.dialogParam.display = !event;
    this.getProcessInstanceList(this.queryApprovalInstance.processId, this.queryApprovalInstance.processCode, this.queryApprovalInstance.proccessName, this.queryApprovalInstance.startUserId, this.selectedEndType.type, this.querySelectedIsTimeOut.type, this.queryStartTime, this.queryEndTime);
  }


  // 挂起任务恢复
  recoveryHangTask(): void {
    if (this.selectedProcessInstance && (this.selectedProcessInstance.endType !== '1')) {
      showMessage(this.messageService, 'error', '挂起状态才有此功能');
      return;
    }
    const bodyParams = {
      userId: this.loginSys,
      processId: this.selectedProcessInstance.processId,
      // taskId: this.selectedToDoTask.taskId,
      tranType: 'M4'
    };
    this.processInstanceManagementService.taskManage(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.getProcessInstanceList(this.queryApprovalInstance.processId, this.queryApprovalInstance.processCode, this.queryApprovalInstance.proccessName, this.queryApprovalInstance.startUserId, this.selectedEndType.type, this.querySelectedIsTimeOut.type, this.queryStartTime, this.queryEndTime);
          }, true, { message: '恢复成功！', errorMsg: '恢复失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      )
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
