import { Component, OnInit, Input, EventEmitter, Output, OnChanges } from '@angular/core';
import { handleError, handleResult, formatDate, showMessage, findObject } from '../../../common/func/common-func';
import { HangTaskModel } from '../../process-instance-model';
import { MANAGE_TASKS_TYPES, PROCESS_TERMINATED_TYPES } from '../../../common/constant/code.value';
import { MessageService } from 'primeng/components/common/messageservice';
import { ProcessInstanceManagementService } from '../../process-instance-management/process-instance-management.service';
import { CALENDAR_CN, FULL_HORIZONTAL_DATE_FORMAT } from '../../../common/constant/config';
import { DictionaryTypeModel } from '../../../process-definition/process-definition-model';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';

@Component({
  selector: 'app-task-manage',
  templateUrl: './task-manage.component.html',
  styleUrls: ['./task-manage.component.scss']
})
export class TaskManageComponent implements OnInit, OnChanges {


  msgs = [];
  // 接收的传入的流程实例
  @Input() inValue: any;
  // 任务类型
  @Input() taskType: string;

  @Input() isRest: boolean;

  @Output() outValueEventEmitter: EventEmitter<any> = new EventEmitter();
  // 任务挂起实例
  hangTaskInstance: HangTaskModel = new HangTaskModel();
  // 登录用户
  loginSys: string;
  // 控制管理类型
  taskControlType = MANAGE_TASKS_TYPES;
  // 日期格式
  CALENDAR_CN = CALENDAR_CN;
  // 超时截止时间
  outTimeEnd: Date;
  // 流程截止类型集合
  processEndTypes = PROCESS_TERMINATED_TYPES;
  // 选中流程截止类型
  selectedProcessEndType: DictionaryTypeModel = new DictionaryTypeModel();

  dialogParam = {
    display: false,
    headerTitle: ''
  };



  constructor(
    private messageService: MessageService,
    private processInstanceManagementService: ProcessInstanceManagementService,
    private commfunc:Commfunc
  ) {
  }

  ngOnInit() {
    this.loginSys = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
  }

  ngOnChanges() {
    this.resetTaskManage();
  }


  // 保存任务管理
  saveTaskManage(): void {
    let bodyParams = {};
    if (this.taskType === this.taskControlType[0].type) {
      // 取消
      bodyParams = {
        userId: this.loginSys,
        processId: this.inValue.processId,
        taskId: this.inValue.taskId,
        tranType: this.taskType,
        doResion: this.hangTaskInstance.doResion,
        orderNode: this.hangTaskInstance.orderNode
      };
    } else if (this.taskType === this.taskControlType[1].type) {
      // 挂起
      bodyParams = {
        userId: this.loginSys,
        processId: this.inValue.processId,
        taskId: this.inValue.taskId,
        tranType: this.taskType,
        doResion: this.hangTaskInstance.doResion
      };
    } else if (this.taskType === this.taskControlType[2].type) {
      // 终止
      bodyParams = {
        userId: this.loginSys,
        processId: this.inValue.processId,
        taskId: this.inValue.taskId,
        tranType: this.taskType,
        doResion: this.hangTaskInstance.doResion,
        cancelType: this.selectedProcessEndType.type || this.processEndTypes[0].type
      };
    } else if (this.taskType === this.taskControlType[3].type) {
      if (!this.hangTaskInstance.orderNode) {
        showMessage(this.messageService, 'error', '请选择跳转的目标节点！');
        return;
      }
      // 跳转
      bodyParams = {
        userId: this.loginSys,
        processId: this.inValue.processId,
        taskId: this.inValue.taskId,
        tranType: this.taskType,
        doResion: this.hangTaskInstance.doResion,
        targetId: this.hangTaskInstance.orderNode,
        agentUserId: this.hangTaskInstance.orderUser
      };
    } else if (this.taskType === this.taskControlType[5].type) {
      // 更改处理人员
      bodyParams = {
        userId: this.inValue.taskActors,
        processId: this.inValue.processId,
        taskId: this.inValue.taskId,
        tranType: this.taskType,
        doResion: this.hangTaskInstance.doResion,
        agentUserId: this.hangTaskInstance.orderUser
      };
    } else if (this.taskType === this.taskControlType[6].type) {
      // 催办
      bodyParams = {
        userId: this.loginSys,
        processId: this.inValue.processId,
        taskId: this.inValue.taskId,
        tranType: this.taskType,
        askDoInfo: this.hangTaskInstance.askDoInfo
      };
    } else if (this.taskType === this.taskControlType[7].type) {
      const outTime = this.outTimeEnd == null ? null : formatDate(this.outTimeEnd, FULL_HORIZONTAL_DATE_FORMAT);
      // 超时延长
      bodyParams = {
        userId: this.loginSys,
        processId: this.inValue.processId,
        taskId: this.inValue.taskId,
        tranType: this.taskType,
        outTime
      };
    }

    this.commomSaveTaskManage(bodyParams);
  }



  // 保存的通用处理函数
  commomSaveTaskManage(bodyParams: object): void {
    this.processInstanceManagementService.taskManage(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.outValueEventEmitter.emit(event);
        }, true, { message: '保存成功！', errorMsg: '保存失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
          this.outValueEventEmitter.emit(event);
        });
      }
      )
  }



  // 重置任务配置
  resetTaskManage(): void {
    this.hangTaskInstance.doResion = null;
    this.hangTaskInstance.orderNode = null;
    this.hangTaskInstance.orderUser = null;
    this.hangTaskInstance.askDoInfo = null;
    this.selectedProcessEndType = findObject('2', this.processEndTypes, 'type');
    this.outTimeEnd = null;
  }


  // 选择目标节点
  selectedTargetNode(targetNode): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = `任务ID为【${this.inValue.taskId}】的节点列表`;
    targetNode.getTargetNodeList(this.inValue.taskId)
  }


  // 从节点上传上来的值
  callBack(event): void {
    console.log(event);
    this.dialogParam.display = !event;
    this.hangTaskInstance.orderNode = event.nodeId;
  }

}
