import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
import { SEND_MESSAGE_API } from '../process-instance.constant';
// import { RTS_HTTP_URL } from '../../common/constant/config';

@Injectable()
export class MessageSendingQueryService {

  constructor(
    private httpService: HttpService
  ) {
  }

  // 查询消息发送列表
  listWfMessage = (params) => this.httpService.post(RTS_HTTP_URL + SEND_MESSAGE_API.listWfMessage, params, null);

}
