import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { Pager, RULE_ENGINE_HTTP_URL, RULE_ENGINE_CONTEXT_PATH_RULE_EDITOR, PARAM_PACKAGE, CONSTANT_PACKAGE, VARIABLE_PACKAGE, RULE_PACKAGE, RESOURCE_PACKAGE, paginatorUtils, FULL_HORIZONTAL_DATE_FORMAT } from '../../../common/constant/config';
import { TreeNode, ConfirmationService, MenuItem } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, expandLastNode, handleResult, conversionExtToNgTree, handleResult2, findObject, formatDate, showMessage } from '../../../common/func/common-func';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { DomSanitizer } from "@angular/platform-browser";
import { ProcessInstanceManagementService } from '../process-instance-management.service';
import { invalid } from 'moment';
import { ApprovedModel } from '../../process-instance-model';
import { TASK_NODE_TYPES, WORK_HANDLE_STATES, APPROVE_RESULT, ADVANCE_LEVEL, VIEW_FLAG } from '../../../common/constant/code.value';

@Component({
  selector: 'app-approval-records',
  templateUrl: './approval-records.component.html',
  styleUrls: ['./approval-records.component.scss'],

})
export class ApprovalRecordsComponent implements OnChanges {

  msgs = [];
  @Input() inValue: any;

  @Input() isChange: boolean;

  // 查询到的已审批列表
  queryApproveds: ApprovedModel[] = [];
  // 类型
  businessNodeTypes = TASK_NODE_TYPES;
  // 当前任务处理状态
  processState = WORK_HANDLE_STATES;
  // 审批结果
  approveResult = APPROVE_RESULT;
  // 分页
  pager: Pager = new Pager();
  // 控制子流程审批列表显隐
  childDisplay = false;

  isChildChange = false;

  // 子流程嵌套数组
  approvalRecorsArray = [
    { show: false, taskId: '', title: '子流程审批信息' }
  ]

  // 紧急程度
  taskUrgent = ADVANCE_LEVEL;
  // 查看状态
  VIEW_FLAG = VIEW_FLAG;
  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private processInstanceManagementService: ProcessInstanceManagementService,
  ) {

  }

  ngOnChanges() {
    this.pager.page = '1';
    this.pager.first = '0';
    this.inValue && this.inValue.processId && this.getApprovedList(this.inValue.processId);
  }



  // 获取审批记录列表
  getApprovedList(processId: string): void {
    if (!processId) {
      return;
    }
    const bodyParams = {
      processId,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.processInstanceManagementService.queryApprovedList(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.queryApproveds = data.pageData[0].result;
          this.pager.totalRecords = data.pageData[0].totalRows;
          this.queryApproveds && this.queryApproveds.length && this.queryApproveds.map(item => {
            item.customdoTaskActor = item.workState === '0' ? item.taskActorsName : item.doTaskActorName;
          });
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }

  // 分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getApprovedList(this.inValue.processId);
  }



  viewChildApproveList(item): void {
    this.isChildChange = !this.isChildChange;
    this.approvalRecorsArray[0].show = true;
    this.approvalRecorsArray[0].taskId = item.taskId;

  }

  callBackChildApprovalRecord(event): void {
    this.approvalRecorsArray.push({ show: true, taskId: event, title: '子流程审批列表' });
  }



}
