import { Component, OnInit } from '@angular/core';
import { CheckTaskModel } from '../process-instance-model';
import { paginatorUtils, CALENDAR_CN, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { ConfirmationService } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, handleResult, formatDate } from '../../common/func/common-func';
import { DictionaryTypeModel } from '../../process-definition/process-definition-model';
import { TASK_NODE_TYPES, WORK_HANDLE_STATES, IS_PROCESS_END, TASK_TYPES, TASK_URGENT, TASK_END_TYPES, APPROVE_RESULT, PROCESS_ISTIMEOUT } from '../../common/constant/code.value';
import { CheckTaskQueryService } from '../check-task-query/check-task-query.service';

@Component({
  selector: 'app-already-tasks-query',
  templateUrl: './already-tasks-query.component.html',
  styleUrls: ['./already-tasks-query.component.scss']
})
export class AlreadyTasksQueryComponent implements OnInit {


  msgs = [];

  CALENDAR_CN = CALENDAR_CN;

  queryApproveds: CheckTaskModel[] = [];

  queryToDoTask: CheckTaskModel = new CheckTaskModel();
  // 分页实例
  paginatorInstance: CheckTaskModel = new CheckTaskModel();
  // 数据总量
  total: string;
  // 登录名
  loginSys: string = '';
  // 流程是否结束
  processEndFlags = IS_PROCESS_END;
  // 查询中选中的流程是否结束标志
  selectedEndFlag: DictionaryTypeModel = new DictionaryTypeModel();
  // 流程结束类型
  processEndTypes = TASK_END_TYPES;
  // 查询中选中的流程结束类型
  selectedEndType: DictionaryTypeModel = new DictionaryTypeModel();
  // 节点类型
  businessNodeTypes = TASK_NODE_TYPES;
  // 查询中选中的节点类型
  selectedQueryTaskType: DictionaryTypeModel = new DictionaryTypeModel();
  // 审批记录列表表头
  approveHeaderText: string;
  // 当前任务处理状态
  processState = WORK_HANDLE_STATES;
  // 点击审批记录按钮
  showRecords: number;
  // 查询的实例id
  queryInstanceId: string;
  // 查询的流程编码
  queryProcessCode: string;
  // 查询的处理人员
  queryDoDeal: string;
  // 查询开始时间
  queryCreateTime: Date;
  // 任务生成开始时间
  queryStartTimeStart: Date;
  // 任务生成截止时间
  queryStartTimeEnd: Date;
  // 任务到期开始时间
  queryEndTimeStart: Date;
  // 任务到期截止时间
  queryEndTimeEnd: Date;

  // 任务类型
  taskTypes = TASK_TYPES;
  // 任务紧急程度
  taskUrgents = TASK_URGENT;
  // 审批结果
  approveResult = APPROVE_RESULT;
  // 查询中选中的处理结果
  selectedQueryApproveResult: DictionaryTypeModel = new DictionaryTypeModel();

  // 是否超时
  isTimeOuts = PROCESS_ISTIMEOUT;
  // 查询条件中选中的是否超时
  querySelectedIsTimeOut: DictionaryTypeModel = new DictionaryTypeModel();

  constructor(
    private messageService: MessageService,
    private checkTaskQueryService: CheckTaskQueryService,

  ) {}

  ngOnInit() {
    this.loginSys = sessionStorage.getItem('sys');
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
    this.showRecords = 0;
  }

  // 按照搜索条件查询
  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }

  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getToDoTasksList(this.queryToDoTask.processName, this.queryToDoTask.processId, this.selectedQueryTaskType.type, this.queryToDoTask.taskName, this.queryToDoTask.doTaskActor, this.selectedQueryApproveResult.type, this.queryStartTimeStart, this.queryStartTimeEnd, this.queryEndTimeStart, this.queryEndTimeEnd, this.querySelectedIsTimeOut.type, this.queryToDoTask.taskId);
  }

  // 查询的重置
  reset(): void {
    this.queryToDoTask.processCode = null;
    this.queryToDoTask.processName = null;
    this.queryToDoTask.workCreater = null;
    this.queryToDoTask.processId = null;
    this.queryToDoTask.taskName = null;
    this.queryToDoTask.doTaskActor = null;
    this.queryToDoTask.taskId = null;
    this.selectedQueryTaskType = new DictionaryTypeModel();
    this.selectedQueryApproveResult = new DictionaryTypeModel();
    this.querySelectedIsTimeOut = new DictionaryTypeModel();
    this.queryStartTimeStart = null;
    this.queryStartTimeEnd = null;
    this.queryEndTimeStart = null;
    this.queryEndTimeEnd = null;
  }


  // 获取已办任务列表
  getToDoTasksList(processName?: string, processId?: string, nodeType?: string, nodeName?: string, doTaskActor?: string, doResult?: string, queryStartTimeStart?: any, queryStartTimeEnd?: any, queryEndTimeStart?: any, queryEndTimeEnd?: any, isTimeout?: string, taskId?: string): void {
    const createBeginTime = queryStartTimeStart == null ? null : formatDate(queryStartTimeStart, FULL_HORIZONTAL_DATE_FORMAT);
    const startBeginTime = queryStartTimeStart == null ? null : formatDate(queryStartTimeStart, FULL_HORIZONTAL_DATE_FORMAT);
    const createEndTime = queryStartTimeEnd == null ? null : formatDate(queryStartTimeEnd, FULL_HORIZONTAL_DATE_FORMAT);
    const startEndTime = queryStartTimeEnd == null ? null : formatDate(queryStartTimeEnd, FULL_HORIZONTAL_DATE_FORMAT);    
    const closeBeginTime = queryEndTimeStart == null ? null : formatDate(queryEndTimeStart, FULL_HORIZONTAL_DATE_FORMAT);
    const closeEndTime = queryEndTimeEnd == null ? null : formatDate(queryEndTimeEnd, FULL_HORIZONTAL_DATE_FORMAT);
    const bodyParams = {
      processName,
      processId,
      nodeType,
      nodeName,
      doTaskActor,
      doResult,
      workState: '1',
      startBeginTime,
      startEndTime,
      closeBeginTime,
      closeEndTime,
      isTimeout,
      taskId,
      // userId: this.loginSys,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.checkTaskQueryService.getCheckTasks(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.queryApproveds = data.pageData[0].result;

          this.queryApproveds && this.queryApproveds.length && this.queryApproveds.map(item => {

            item.customSystemCode = item.processCode.split(":")[0];

            item.customWorkCreater = item.workCreater ? item.workCreaterName ? `${item.workCreater} (${item.workCreaterName})` : `${item.workCreater}` : '';

            item.customDoTaskActorName = item.doTaskActor ? item.doTaskActorName ? `${item.doTaskActor} (${item.doTaskActorName})` : `${item.doTaskActor}` : '';

          });
          this.total = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }

}
