import { Component, OnInit } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, handleResult, formatDate, findObject } from '../../common/func/common-func';
import { DictionaryTypeModel } from '../../process-definition/process-definition-model';
import { MessageSendingQueryService } from '../../process-instance/message-sending-query/message-sending-query.service';
import { Pager } from '../../common/constant/config';
import { SEND_RESULT_TYPE, SEND_MESSAGE_TYPE } from '../../common/constant/code.value';
import { SendMessageModel } from '../../process-instance/process-instance-model';

@Component({
  selector: 'app-message-sending-query',
  templateUrl: './message-sending-query.component.html',
  styleUrls: ['./message-sending-query.component.scss']
})
export class MessageSendingQueryComponent implements OnInit {

  msgs = [];
  // 分页
  pager: Pager = new Pager();
  // 发送结果
  sendResults = SEND_RESULT_TYPE;
  // 发送类型
  sendTypes = SEND_MESSAGE_TYPE;
  // 查询条件中选中的发送类型
  selectedSendType: DictionaryTypeModel = new DictionaryTypeModel();
  // 查询回来的消息列表
  messageSendList: SendMessageModel[] = [];
  // 查询的发送消息实例
  queryMessageSend: SendMessageModel = new SendMessageModel();






  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private messageSendingQueryService: MessageSendingQueryService,

  ) {


  }

  ngOnInit() {
    this.getSendMessageList(this.queryMessageSend.smsCotent, this.selectedSendType.type, this.queryMessageSend.taskId, this.queryMessageSend.userId);
  }

  // 按照搜索条件查询
  query(): void {
    this.pager.first = '0';
    this.pager.page = '1';
    this.getSendMessageList(this.queryMessageSend.smsCotent, this.selectedSendType.type, this.queryMessageSend.taskId, this.queryMessageSend.userId);
  }

  // 查询的重置
  reset(): void {
    this.queryMessageSend.smsCotent = null;
    this.queryMessageSend.taskId = null;;
    this.queryMessageSend.userId = null;
    this.selectedSendType = new DictionaryTypeModel();
  }

  // 分页
  paginate(event) {
    console.log(event.first)
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getSendMessageList(this.queryMessageSend.smsCotent, this.selectedSendType.type, this.queryMessageSend.taskId, this.queryMessageSend.userId);
  }


  // 获取已办任务列表
  getSendMessageList(smsCotent?: string, type?: any, taskId?: string, userId?: string): void {
    const bodyParams = {
      smsCotent,
      type,
      taskId,
      userId,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.messageSendingQueryService.listWfMessage(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.messageSendList = data.pageData[0].result;
          this.pager.totalRecords = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }

}
