import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
import { RTS_HTTP_URL, ACTIVITI_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
import { EXCEPTION_API } from '../process-instance.constant';
import { ACTIVITI_CONTEXT_PATH, ACTIVITI_SCHEDULE_CONTEXT_PATH } from '../../common/constant/config'
;
// import { RTS_HTTP_URL } from '../../common/constant/config';

@Injectable()
export class 
ManualExceptionInformationService {

  constructor(
    private httpService: HttpService
  ) {
  }

  // 查询异常信息列表
  getExceptionList = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPTION_API.selectList, params, null);

  // 异常管理
  dealExpDo = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPTION_API.dealExpDo, params, null);

  // 根据时间ID获取事件服务
  selectById = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPTION_API.selectById, params, null);

  // 忽略功能
  updateExceptionData = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPTION_API.updateExceptionData, params, null);

  // 跳转到流程设计器
  toActivitiSchedule = (params) => window.open(ACTIVITI_HTTP_URL + `/${params}`);
  
  // 查询查看流程图的url
  getShowProcessUrl = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPTION_API.getShowProcessUrl, params, null);
}
